<template>
  <div>
    <a id="nav-button" :class="{ open: navOpen }">
      <span @click="navOpen = !navOpen"> NAV <img :src="$getAsset('navbar.png')"> </span>
    </a>
    <div :class="['tocify-wrapper', { open: navOpen }]">
      <router-link to="/">
        <img :src="$getAsset('ijs.svg')" class="logo" alt="interact.js" title="homepage">
      </router-link>

      <div class="has-text-centered">
        <VersionBadge/>
      </div>

      <div class="search">
        <input type="text" class="search" id="input-search" placeholder="Search">
      </div>
      <ul class="search-results"></ul>

      <div ref="toc" id="toc">
      </div>

      <br class="is-hidden-tablet">

      <nav class="navbar is-hidden-tablet" role="navigation" aria-label="navigation">
        <HeaderLinks class="navbar-end"/>
      </nav>

    </div>
    <div class="page-wrapper">
      <nav class="navbar is-dark is-hidden-mobile" role="navigation" aria-label="navigation">
        <HeaderLinks mobile class="navbar-end is-flex"/>
      </nav>

      <div class="dark-box">
        <div class="ad-wrap">
          <div ref="adSlot" id="ad-contain">
          </div>
        </div>
      </div>

      <keep-alive :max="3" :key="$route.path">
        <router-view class="docs-content">
        </router-view>
      </keep-alive>

      <div class="ad-padding"/>
    </div>
  </div>
</template>

<style lang="sass" src="@/sass/slate.sass"/>

<script>
import Vue from 'vue'

export default Vue.extend({
  data () {
    return { navOpen: false }
  },
  computed: {
    path () { return this.$route.path }
  },
  watch: {
    path () {
      this.initToc()
    }
  },
  methods: {
    async initToc () {
      if (this.toc) { this.toc.destroy() }
      if (this._inactive) { return }

      const { default: tocbot } = await import('tocbot')

      this.toc = tocbot.init({
        tocSelector: '#toc',
        headingSelector: 'h1, h2, h3',
        contentSelector: '.page-wrapper',
        ignoreSelector: '.js-toc-ignore',
        scrollSmooth: true,
        scrollSmoothDuration: 420,
        collapseDepth: 6
      })
    },
    appendAd () {
      if (this._isMounted) {
        this.$refs.adSlot.appendChild(window.__adWrapper)
      }
    }
  },
  mounted () {
    this.initToc()
    this.appendAd()
  },
  beforeRouteEnter (to, from, next) {
    next(vm => vm.appendAd())
  },
  beforeRouteLeave (to, from, next) {
    this.toc.destroy()
    next()
  },
  destroyed () {
    this.toc.destroy()
    this.toc = null
  }
})
</script>
