import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home.vue'
import Docs from '../views/Docs.vue'
import DocsApi from '../views/DocsApi.vue'
import docsPages from './docsPages.js'
import apiPages from './apiPages.js'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/docs',
      component: Docs,
      children: Object.entries(docsPages).map(([path, component]) => {
        return { path, component }
      })
    },
    {
      path: '/docs/api/',
      component: DocsApi,
      children: Object.entries(apiPages).map(([path, component]) => {
        return { path, component }
      })
    }
  ]
})
