<template>
  <div>
    <Hero>
    <div class="columns">
      <div class="column is-half">

        <div class="joiner is-size-4 is-hidden-tablet"></div>
        <h1 class="title is-1 is-size-4-mobile has-text-centered-mobile">
          JavaScript drag and drop, resizing, and multi-touch gestures
          for modern browsers (and&nbsp;also&nbsp;IE9+)
        </h1>

        <div class="has-text-centered-mobile">
          <VersionBadge/>
        </div>

        <div class="spacer is-hidden-mobile"></div>

        <Features class="is-hidden-mobile"/>
      </div>

      <div class="column is-half anim-column">
        <Animations ref="animations"/>
      </div>

    </div>

    <div ref="adSlot"/>

    </Hero>

    <div class="container">
      <div class="joiner is-size-3 is-double is-hidden-mobile"></div>

      <section class="is-hidden-tablet">
        <div id="mobile-features" class="container">
          <Features/>
        </div>
      </section>

      <div class="joiner is-size-1 is-hidden-mobile"></div>

      <LiveDemo v-for="demo of demos" :key="demo" :demoHtml="demo" class="box-mobile"/>
    </div>
    <Footer/>

      <a :class="['scroll-top', 'is-hidden-tablet', { hidden: !showScrollIcon }]" href="#top" title="Back to top">
      <img class="scroll-top-icon" :src="$getAsset('up.svg')" alt="Back to top">
    </a>
  </div>
</template>

<script>
import Vue from 'vue'

export default Vue.extend({
  data () {
    return {
      demos: [],
      showScrollIcon: false
    }
  },
  methods: {
    appendAd () {
      if (this._isMounted) {
        this.$refs.adSlot.appendChild(window.__adWrapper)
      }
    }
  },
  mounted () {
    const { animations } = this.$refs

    animations.updateCodeFrameSize(npm + importFrom + draggable)

    timeout(1000).then(animations.start({ '': async () => {
      await animations.codeDemo({ code: npm, delimiter: 'character', noUpdateSize: true })
      await timeout(300)
      await animations.codeDemo({ code: importFrom, lang: 'javascript', append: true, delimiter: 'character' })
      await timeout(200)
      await animations.codeDemo({ code: draggable, lang: 'javascript', append: true })
    } }))

    import(/* webpackChunkName: "home-demos" */ './homeDemos').then(({ default: demos }) => {
      this.demos = demos
    })

    this.onScroll = () => {
      const newValue = window.scrollY > 300

      if (newValue === this.showScrollIcon) { return }

      this.showScrollIcon = newValue
    }

    window.addEventListener('scroll', this.onScroll)

    this.appendAd()
  },

  beforeRouteEnter (to, from, next) {
    next(vm => vm.appendAd())
  },

  destroyed () {
    window.removeEventListener('scroll', this.onScroll)
  }
})

const npm = '$ npm install interactjs@next'
const importFrom = '\n\nimport interact from \'interactjs\''
const draggable = `\n\ninteract('.item').draggable({
  onmove(event) {
    console.log(event.pageX,
                event.pageY)
  }
})`

function timeout (duration) {
  return new Promise(resolve => setTimeout(resolve, duration))
}
</script>
