export default {
  '': () => getComponent(import('../docs/api/index.html')),
  '-_actions_drag.ts.html': () => getComponent(import('../docs/api/-_actions_drag.ts.html')),
  '-_actions_drop_DropEvent.ts.html': () => getComponent(import('../docs/api/-_actions_drop_DropEvent.ts.html')),
  '-_actions_drop_index.ts.html': () => getComponent(import('../docs/api/-_actions_drop_index.ts.html')),
  '-_actions_gesture.ts.html': () => getComponent(import('../docs/api/-_actions_gesture.ts.html')),
  '-_actions_resize.ts.html': () => getComponent(import('../docs/api/-_actions_resize.ts.html')),
  '-_auto-start_base.ts.html': () => getComponent(import('../docs/api/-_auto-start_base.ts.html')),
  '-_auto-start_InteractableMethods.ts.html': () => getComponent(import('../docs/api/-_auto-start_InteractableMethods.ts.html')),
  '-_core_interactablePreventDefault.ts.html': () => getComponent(import('../docs/api/-_core_interactablePreventDefault.ts.html')),
  '-_core_Interactable.ts.html': () => getComponent(import('../docs/api/-_core_Interactable.ts.html')),
  '-_core_InteractEvent.ts.html': () => getComponent(import('../docs/api/-_core_InteractEvent.ts.html')),
  '-_core_Interaction.ts.html': () => getComponent(import('../docs/api/-_core_Interaction.ts.html')),
  'DropEvent.html': () => getComponent(import('../docs/api/DropEvent.html')),
  'global.html': () => getComponent(import('../docs/api/global.html')),
  'index.html': () => getComponent(import('../docs/api/index.html')),
  'Interactable.html': () => getComponent(import('../docs/api/Interactable.html')),
  'InteractEvent_InteractEvent.html': () => getComponent(import('../docs/api/InteractEvent_InteractEvent.html')),
  '-_interact_interact.ts.html': () => getComponent(import('../docs/api/-_interact_interact.ts.html')),
  '_modifiers_snap_edges.ts.html': () => getComponent(import('../docs/api/-_modifiers_snap_edges.ts.html')),
  'module.exports.html': () => getComponent(import('../docs/api/module.exports.html')),
  'module-interact.html': () => getComponent(import('../docs/api/module-interact.html')),
  'module-modifiers_snapEdges.html': () => getComponent(import('../docs/api/module-modifiers_snapEdges.html')),
  '_pointer-events_PointerEvent.ts.html': () => getComponent(import('../docs/api/-_pointer-events_PointerEvent.ts.html')),
  '_reflow_index.ts.html': () => getComponent(import('../docs/api/-_reflow_index.ts.html'))
}

async function getComponent (modulePromise) {
  const html = (await modulePromise).default

  return {
    data () {
      return { html }
    },
    render (createElement) {
      return createElement('div', {
        class: { 'jsdoc-content': true },
        domProps: { innerHTML: html }
      })
    },
    mounted () {
      const { tocifyWrapper } = this.$parent.$refs
      const nav = this.$el.querySelector('nav')

      tocifyWrapper.appendChild(nav)

      for (const heading of this.$el.querySelectorAll('h1, h2, h3, h4, h5, h6')) {
        const level = heading.tagName.match(/\d/)[0]

        heading.classList.add('title')
        heading.classList.add(`is-${parseInt(level, 10) + 1}`)
      }

      for (const table of this.$el.querySelectorAll('table')) {
        table.classList.add('table')
      }
    }
  }
}
